//  OpenWeatherMap API 
const WEATHER_API_KEY = "YOUR_OPENWEATHERMAP_API_KEY";

// DOM 
const findRamenBtn = document.getElementById("find-ramen-btn");
const locationInput = document.getElementById("location-input");
const weatherInfo = document.getElementById("weather-info");
const shopList = document.getElementById("shop-list");

// 
const ramenShops = [
  { name: "", address: " 123 " },
  { name: "", address: " 456 " },
  { name: "", address: " 789 " },
];

// 
async function getWeather(location) {
  try {
    const response = await fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=${location}&appid=${WEATHER_API_KEY}&units=metric`
    );
    const data = await response.json();

    if (data.cod === 200) {
      weatherInfo.textContent = `${data.main.temp}C${data.weather[0].description}`;
    } else {
      weatherInfo.textContent = "";
    }
  } catch (error) {
    weatherInfo.textContent = "";
  }
}

// 
function displayRamenShops() {
  shopList.innerHTML = ""; // 
  ramenShops.forEach((shop) => {
    const listItem = document.createElement("li");
    listItem.textContent = `${shop.name} - ${shop.address}`;
    shopList.appendChild(listItem);
  });
}

// 
findRamenBtn.addEventListener("click", () => {
  const location = locationInput.value;
  if (!location) {
    alert("");
    return;
  }

  // 
  getWeather(location);

  // 
  displayRamenShops();
});
